*{
    font-family: 'Montserrat', sans-serif;
}

html, body {
    background-color: "#FFF";
    overflow-x: hidden;
    /* background-color: black */
}



@tailwind base;
@tailwind components;
@tailwind utilities;

.portfolio-intro {
    background-color: #38b44a;
    background-image: linear-gradient(315deg, #38b44a, #ec9f05 74%);
    overflow-x: hidden !important;
    transform: rotate(12deg);
    position: absolute;
    top: -30%;
    left: -100px;
    right: 0;
    border-radius: 50px
  }

  html, body {
    background-color: "#FFF";

}